
.Header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
}

.Title {
    display: flex;
    flex-direction: row;

    align-items: center;

    gap: 10px;
}


@media only screen and (max-width: 800px) {
    .Header {
        flex-direction: column;
        align-items: flex-start;
    }
}
